.apply {
	margin-top: 25px;
	position: relative;
	.icon-arrow-down {
		fill: #1b2125;
		width: 12px; }
	&__form {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-right: 34px;
		width: 100%; }
	&__search {
		width: 100%;
		padding: 14px 17px 12px;
		border-radius: 5px 0 0 5px;
		color: $bluey-grey;
		font-size: 16px;
		box-shadow: -4px 4px 15px rgba(0,0,0,.1);
		&::placeholder {
			color: $bluey-grey; } }
	&__button {
		background: none;
		border: none;
		padding: 14px 22px 12px;
		font-size: 16px;
		background-color: $iris;
		color: #fff;
		text-transform: uppercase;
		border-radius: 0 5px 5px 0;
		box-shadow: 0px 4px 15px rgba(0,0,0,.2); }
	&__arrow-down {
		position: absolute;
		right: 0;
		top: 50%;
		margin-top: -12px; } }
