.btn {
	display: inline-block;
	vertical-align: top;
	padding: 13px 35px;
	border-radius: 5px;
	background: $dusk;
	box-shadow: 0 4px 8px 0 rgba($black, .06);
	@include f(14px, 606, 1);
	letter-spacing: 1.2px;
	color: $white;
	text-align: center;
	text-transform: uppercase;
	transition: color 0.25s, background 0.25s;
	&:hover {
		background: $white;
		color: $iris; }
	&[disabled],
	&:disabled {
		background: $dusk;
		color: $dark-grey-blue-two;
		cursor: not-allowed;
		@include hover {
			background: $dusk;
			color: $dark-grey-blue-two; } } }
