.aside {
	width: 413px;
	padding: 40px;
	@include md {
		width: 100%;
		padding: 40px 20px; }
	&__container {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 100%; }
	&__head {
		border-bottom: 1px 	solid $white-two;
		padding: 0 0 24px;
		margin-bottom: 32px; }
	&__field {
		&:not(:last-child) {
			margin-bottom: 20px; } }
	&__wrap {
		margin: 32px 10px; }
	&__row {
		width: 100%; }
	&__footer {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin: 0 10px; } }
